.body {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-column-gap: 40px;
    margin: 20px 0 0 0;

    @media (max-width:900px) {
        grid-template-columns: 1fr 1fr;
    }

    @media (max-width:768px) {
        display: flex;
        flex-direction: column;
    }
}

.left-column {
    &__title {
        font-size: 67px;
        text-align: center;

        @media (max-width:1200px) {
            font-size: 59px;
        }

        @media (max-width:1024px) {
            font-size: 45px;
        }

        @media (max-width:568px) {
            font-size: 33px;
        }
    }
}

h1 {
    font-family: 'Handshoptypography';
}

h2 {
    font-family: 'Oldnewspapertypes';
    font-weight: 900;
}

.subtitle {
    font-family: "Averiaserif";
    font-weight: 600;
    text-align: center;
}

.first-articles {
    &__paragraph {
        margin: 15px 0;

        @media (max-width:1300px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__top-article {
        text-align: center;

        @media (max-width: 1300px) {
            float: right;
            text-align: center;
        }
    }

    &__title {
        font-size: 25px;
        text-align: justify;
        width: 100%;
        font-family: 'Averiaserif';
        font-weight: 700;

        @media (max-width: 568px) {
            font-size: 20px;
        }
    }

    &__frame-img {
        float: left;
        margin: 0 20px 0 0;
    }

    &__articles-text-container {
        @media (max-width:1300px) {
            clear: both;
        }
    }
}

.articles-text {
    margin-bottom: 15px;
    text-align: justify;
    font-family: 'Averiaserif';
}

.frame-img {
    border: 3px solid #000;
    position: relative;
    padding: 3px;
}

.frame-img:before {
    position: absolute;
    display: block;
    content: '';
    border: 1px solid black;
    height: 96%;
    width: 98%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.underline-black-bold {
    margin: 25px 0 0 0;
    position: relative;
    padding: 3px;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        top: 0;
        right: 0;
        height: 3px;
        background-color: #000;
    }
}

.underline-grey-bold {
    margin: 0 0 25px 0;
    position: relative;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        top: 0;
        right: 0;
        height: 3px;
        background-color: grey;
    }
}

.second-articles {
    &__title {
        font-size: 38px;
        text-align: center;
        margin: 0 0 5px 0;

        @media (max-width:1200px) {
            font-size: 32px;
        }

        @media (max-width:1024px) {
            font-size: 27px;
        }

        @media (max-width:900px) {
            font-size: 24px;
        }

        @media (max-width:568px) {
            font-size: 18px;
        }
    }

    &__subtitle {
        display: flex;
        justify-content: space-between;
        font-size: 37px;
        margin: 0 0 25px;

        @media (max-width:1200px) {
            font-size: 32px;
        }

        @media (max-width:1024px) {
            font-size: 26px;
        }

        @media (max-width:900px) {
            font-size: 23px;
        }

        @media (max-width:768px) {
            justify-content: center;
        }

        @media (max-width:568px) {
            font-size: 17px;
        }

    }

    &__frame-img {
        margin: 0 0 0 20px;
        float: right;

        @media (max-width:900px) {
            margin: 0 0 20px;
        }
    }

    &__frame-img:before {
        position: absolute;
        display: block;
        content: '';
        border: 1px solid black;
        height: 98%;
        width: 97%;
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
    }

    &__paragraph {
        @media (max-width:900px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}


.third-articles {

    @media (max-width:1300px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    @media (max-width:900px) {
        display: none;
    }

    &__frame-img {
        float: left;
        margin: 0 20px 0 0;

        @media (max-width:1300px) {
            margin: 0 0 20px 0;
        }
    }

    &__title {
        font-size: 25px;
        text-align: center;
        font-family: "Averiaserif";
        font-weight: 700;
        padding: 3px 0;
    }

    &__container {
        display: flex;
        flex-direction: column;

        @media (max-width:1300px) {
            display: block;
            float: left;
        }
    }

}

.unerline-black-normal {
    position: relative;

    &:after {
        content: '';
        position: absolute;
        width: 100%;
        bottom: 0;
        right: 0;
        height: 2px;
        background-color: #000;
    }

    &:before {
        content: '';
        position: absolute;
        width: 100%;
        bottom: 0;
        right: 0;
        height: 2px;
        background-color: #000;
    }
}

.first-data-media {
    display: none;

    @media (max-width:900px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}